@import url(https://fonts.googleapis.com/css?family=Grand+Hotel&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap);
/* Logo --> font-family: 'Grand Hotel' */
/* Everything Else -->*/

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

ul,
li,
a {
  text-decoration: none;
  list-style: none;
  color: inherit;
}
.fas.fa-shopping-bag {
  position: relative;
  margin: auto;
}
.fa-shopping-bag::before {
  max-height: 100%;
  position: absolute;
  color: #b99867;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  font-size: 4em;
}

/* top: calc(50% - 30px);
left: calc(50% - 22px);

font-size: 4.2em; */

aside.cart-popout-modal {
  position: fixed;
  top: 0;
  right: 0;
  width: 350px;
  height: 100%;
  background: #fff;

  z-index: 999;
  -webkit-animation: modal-onEnter 1s forwards;
          animation: modal-onEnter 1s forwards;
}
.cart-modal-header {
  width: 100%;
  height: 100px;
  background: #294057;
  line-height: 100px;
  text-align: center;
  color: #fff;
}
.cart-modal-header span {
  position: absolute;
  left: 15px;
  top: calc(50px - 10px);
  width: 20px;
  height: 20px;
  cursor: pointer;
  line-height: 20px;
  font-size: 1.3em;
  -webkit-animation: modalArrow-onEnter 1s forwards;
          animation: modalArrow-onEnter 1s forwards;
}
aside.cart-popout-modal main {
  position: relative;
  max-height: 100%;
  overflow: hidden;
}
.cart-modal-product {
  position: relative;
  height: calc(100vh - 100px - 246px);
  overflow: auto;
  padding: 0 1.9em;
}
/* .cart-modal-product li[data-active='false'] .item-header span {
  color: rgba(0, 0, 0, 0);
} */
.cart-modal-product ul {
  position: relative;
  list-style: none;
  width: 100%;
  height: 100%;
}
.cart-modal-item {
  position: relative;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  padding: 1em 1em;
}
.cart-modal-item:last-child {
  border: none;
}

.item-wrapper {
  display: flex;
  justify-content: left;
  align-items: center;
}
aside.item-image {
  min-width: 70px;
  height: 70px;
  margin-right: 20px;
  cursor: pointer;
}
.item-info {
  position: relative;
  width: 100%;
  height: 100%;
}
.item-header {
  padding-bottom: 0.3em;
  color: #000;
}
.item-info p {
  padding: 0.2em 0;
  font-weight: 900;
  font-size: 0.9em;
}
.item-header h2 {
  font-size: 1.1em;
  font-weight: 300;
}
.item-header span {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.1em;
  color: rgba(0, 0, 0, 0);
  cursor: pointer;
  transition: 0.3s linear all;
  line-height: 24px;
}
.cart-modal-item:hover .item-header span {
  color: rgba(0, 0, 0, 0.7);
}
.cart-modal-total {
  padding: 1em 2em 2.5em;
  border-bottom: 1px solid #000;
  font-size: 1.2em;
}
.cart-modal-total h1 {
  font-weight: 300;
}
.modal-footer {
  text-align: center;
  background: #fff;
  height: 100px;
  width: 100%;
  line-height: 100px;
}
@-webkit-keyframes modalArrow-onEnter {
  from {
    transform: rotateZ(180deg);
  }
  to {
    transform: rotateZ(0deg);
  }
}
@keyframes modalArrow-onEnter {
  from {
    transform: rotateZ(180deg);
  }
  to {
    transform: rotateZ(0deg);
  }
}
@-webkit-keyframes modalArrow-onExit {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(180deg);
  }
}
@keyframes modalArrow-onExit {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(180deg);
  }
}
@-webkit-keyframes modal-onEnter {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes modal-onEnter {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@-webkit-keyframes modal-onExit {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}
@keyframes modal-onExit {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}

